@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: 'Rubik', sans-serif;
}

body {
  background-color: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

* {
  margin: 0;
  padding: 0;
  font-weight: 500;
}

.result {
  background-color: #858694;
  padding: 20px 10px;
  text-align: right;
  color: white;
}

.calculator {
  display: -ms-grid;
  display: grid;
  grid-template-columns: auto auto auto auto;
  -ms-grid-template-columns: auto auto auto auto;
}

button {
  border-width: 1px;
  border-color: #e4e4e4;
  padding: 1.5rem;
}

.grayButton {
  background-color: #e0e0e0;
}

.orangeButton {
  background-color: #f5913e;
}

.grayButton-0 {
  grid-column: 1/3;
  -ms-grid-column: 1/3;
  background-color: #e0e0e0;
}

.calculatorSection {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 8rem auto;
}

.calculatorSection h1 {
  font-size: 3rem;
  font-weight: 600;
  font-style: oblique;
  border-bottom: 5px solid #f5913e;
}

.calc {
  width: 40%;
  font-size: 20px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #0d19c4;
  padding: 15px 10px;
  color: white;
  font-weight: 600;
}

.navbar ul {
  list-style: none;
  display: flex;
}

.navbar ul > li {
  padding: 5px 10px;
  font-size: 20px;
  border-right: 1px solid white;
}

.navbar ul > li:last-child {
  border-right: none;
}

.navbar a {
  color: white;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 2px;
}

.container {
  text-align: center;
  margin: 5rem auto;
}

.container h1 {
  font-weight: 600;
  letter-spacing: 1px;
  font-style: italic;
  font-size: 3rem;
}

.container p {
  margin: 3rem 10rem;
  letter-spacing: 1px;
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  padding: 2rem;
  border-radius: 4rem;
  line-height: 1.3rem;
}

.container h3 {
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  padding: 1.5rem;
  margin: 3rem 10rem;
  border-radius: 2rem;
}

